  describe: '\u5728\u6211\u4EEC\u4E07\u7EF4\u94FE\u7684\u8BBE\u8BA1\u4E2D\uFF0C\u4ECE\u6280\u672F\u4E0A\u6765\u8BF4\u4E3B\u8981\u662F\u4E24\u4E2A\u65B9\u9762\u7684\u4F5C\u75281\uFF09\u4E07\u7EF4\u94FE\u4E0A\u4EA4\u6613\u53CA\u8DE8\u94FE\u4EA4\u6613\u7684\u624B\u7EED\u8D39\uFF0C2\uFF09\u8D1F\u8D23\u5904\u7406\u8DE8\u94FE\u4EA4\u6613\u7684\u8282\u70B9\u7684\u4FDD\u8BC1\u91D1\u3002\u4ECE\u5E94\u7528\u4E0A\u6765\u8BF4\uFF0C\u968F\u7740\u4E07\u7EF4\u94FE\u5904\u7406\u8DE8\u94FE\u4EA4\u6613\u7684\u589E\u52A0\uFF0C\u4E07\u7EF4\u5E01\u4EF7\u503C\u7684\u589E\u957F\uFF0C\u4E07\u7EF4\u5E01\u53EF\u4EE5\u6210\u4E3A\u8D27\u5E01\u5151\u6362\u6216\u8005\u4EA4\u6613\u7684\u4E2D\u95F4\u8D27\u5E01\uFF0C\u76F8\u5F53\u4E8E\u6210\u4E3A\u5176\u4ED6\u8D27\u5E01\u6216\u6570\u5B57\u8D44\u4EA7\u7684\u4EA4\u6613\u5A92\u4ECB\u3002'
